@font-face {
  font-family: 'MyFont';
  src: url(./fonts/futura.ttf) format('truetype');
}

@font-face {
  font-family: 'MyFont2';
  src: url(./fonts/logotypejp_mp_m_1.1.ttf) format('truetype');
}

html {
  height: 100%;
}

body {
  font-family: 'myfont2', sans-serif;
  width: 100%;
  margin: 0;
  padding: 0;
  background: radial-gradient(#ECDF2B, #ECDF2B);
}

.title {
  justify-content: center;
  align-items: center;
  font-size: 70px;
  font-family: 'myfont', sans-serif;
  text-align: center;
  margin: 3rem 0;
}

.aboutMe {
  display: flex;
  flex-direction: column;
  margin: 3rem 3rem 1rem 3rem;
  font-family: 'myfont', sans-serif;
}

.name {
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 50px;
  margin-top: 0.6rem;
}

.myIconImage {
  margin: 0 auto;
  padding: 0;
  border-radius: 50%;
  width: 200px;
  height: 200px;
}

.allContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1rem;
}

.container {
  display: flex;
  align-items: center;
  width: fit-content;
  margin: auto;
}

.profSentence {
  width: 40%;
  text-align: center;
}

/* make */
.makeList {
  width: 40%;
}

.makeContent {
  background-color: white;
  margin-top: 0.5rem;
  margin-bottom: 1rem;
  padding: 1rem;
}

/* work */
.workList {
  width: 40%;
}

.workContent {
  background-color: white;
  margin-top: 0.5rem;
  margin-bottom: 1rem;
  padding: 1rem;
}

/* skill */
.skillList {
  width: 40%;
}

.skillContent {
  background-color: white;
  margin-top: 0.5rem;
  margin-bottom: 1rem;
  padding: 1rem;
}

/* like */
.likeList {
  width: 40%;
}

.likeContent {
  background-color: white;
  margin-top: 0.5rem;
  margin-bottom: 1rem;
  padding: 1rem;
}

/* link */
.linkList {
  width: 40%;
}

.linkContent {
  background-color: white;
  margin-top: 0.5rem;
  margin-bottom: 1rem;
  padding: 1rem;
}

.positionInput {
  display: flex;
  align-items: center;
  margin: auto;
  width: 35%;
  height: 60px;
  padding: 2px 4px;
}

a.links {
  width: 70px;
  height: 70px;
  margin: 30px;
}

img {
  width: 100%;
  height: 100%;
}

.icons {
  color: black;
  font-size: 70px;
  margin: 0;
}

.mb_01 {
  margin-bottom: 1rem;
}

.fontFutura {
  font-family: 'myfont', sans-serif;
}

.fontLarge {
  font-weight: 900;
  font-size: 17px;
}

.mb0 {
  margin-bottom: 0;
}

.mt2 {
  margin-top: 2rem;
}

.only-sp-show {
  display: none;
}

@media screen and (max-width: 767px) {
  .title {
    top: 150px;
    justify-content: center;
    align-items: center;
    font-size: 40px;
    text-align: center;
  }

  .aboutMe {
    display: flex;
    flex-direction: column;
  }

  .name {
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 40px;
  }

  .myIconImage {
    margin: 0 auto;
    padding: 0;
    border-radius: 50%;
    width: 150px;
    height: 150px;
  }

  .allContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1rem;
  }

  .container {
    display: flex;
    align-items: center;
    width: fit-content;
    margin: auto;
  }

  .profSentence {
    width: 100%;
  }

  /* make */
  .makeList {
    width: 100%;
  }

  .makeContent {
    background-color: white;
    margin-top: 0.5rem;
    margin-bottom: 1rem;
    padding: 1rem;
  }

  /* work */
  .workList {
    width: 100%;
  }

  .workContent {
    background-color: white;
    margin-top: 0.5rem;
    margin-bottom: 1rem;
    padding: 1rem;
  }

  /* skill */
  .skillList {
    width: 100%;
  }

  .skillContent {
    background-color: white;
    margin-top: 0.5rem;
    margin-bottom: 1rem;
    padding: 1rem;
  }

  /* like */
  .likeList {
    width: 100%;
  }

  .likeContent {
    background-color: white;
    margin-top: 0.5rem;
    margin-bottom: 1rem;
    padding: 1rem;
  }

  /* link */
  .linkList {
    width: 100%;
  }

  .linkContent {
    background-color: white;
    margin-top: 0.5rem;
    margin-bottom: 1rem;
    padding: 1rem;
  }

  .positionInput {
    display: flex;
    align-items: center;
  }

  a.links {
    width: 55px;
    height: 55px;
    margin: 15px;
  }

  img,
  i::before {
    margin: 0px;
  }

  .icons {
    font-size: 55px;
    margin: 0px;
  }

  .only-sp-show {
    display: inline;
  }

  /* ハンバーガー
  .MuiGrid-spacing-xs-1 {
    margin: -2px;
  } */
}